import { create } from 'zustand';
import { QUIZ_GROUP_TYPES } from '../models/quiz.js';

/**
 * Quiz Group Store
 * Quáº£n lÃ½ state cá»§a nhÃ³m bÃ i há»c vÃ  bÃ i test
 */
export const useQuizGroupStore = create((set, get) => ({
  // Current group selection
  currentGroup: QUIZ_GROUP_TYPES.OFFICIAL,
  
  // Current subject selection
  currentSubject: null,
  
  // Quiz list for current subject
  quizList: [],
  
  // Loading states
  loadingQuizList: false,
  loadingQuizData: false,
  
  // Actions
  setCurrentGroup: (group) => {
    set({ 
      currentGroup: group,
      currentSubject: null,
      quizList: [],
    });
  },
  
  setCurrentSubject: (subject) => {
    set({ currentSubject: subject });
  },
  
  setQuizList: (quizzes) => {
    set({ quizList: quizzes });
  },
  
  setLoadingQuizList: (loading) => {
    set({ loadingQuizList: loading });
  },
  
  setLoadingQuizData: (loading) => {
    set({ loadingQuizData: loading });
  },
  
  // Clear all
  clearSelection: () => {
    set({
      currentGroup: QUIZ_GROUP_TYPES.OFFICIAL,
      currentSubject: null,
      quizList: [],
    });
  },
}));

